<!-- 这个是个人中心组件页面 -->
<template>
  <transition name="slide">
    <div class="user-center">
      <div class="back" @click="back">
        <i class="icon-back"></i>
      </div>
      <!-- 切换的部分 -->
      <div class="switches-wrapper">
        <switches :currentIndex="currentIndex" :switches="switches" @switchItem="switchItem"></switches>
      </div>
      <div ref="playBtn" class="play-btn" @click="random">
        <i class="icon-play"></i>
        <span class="text">随机播放全部</span>
      </div>
      <div class="list-wrapper" ref="listWrapper">
         <!-- 我喜欢的 -->
          <scroll ref="favoriteList" class="list-scroll" v-if="currentIndex === 0" :data="favoriteList">
            <div class="list-inner">
              <song-list :songs="favoriteList" @select="selectSong"></song-list>
            </div>

          </scroll>
          <!-- 最近听的 -->
          <scroll ref="playHistory" class="list-scroll" v-if="currentIndex === 1" :data="playHistory">
            <div class="list-inner">
              <song-list :songs="playHistory" @select="selectSong"></song-list>
            </div>
          </scroll>
      </div>

      <div class="no-result-wrapper" v-show="noresult">
        <no-result ></no-result>
      </div>
    </div>
  </transition>
</template>

<script>
import Switches from '@/base/switches/switches'
 import Scroll from '@/base/scroll/scroll'
 import SongList from '@/base/song-list/song-list'
import {mapGetters, mapActions} from 'vuex'
import Song from '_common/js/song_Class'
import {playlistMixin} from '@/common/js/mixin'
import NoResult from '@/base/no-result/no-result'

  export default{
    mixins:[playlistMixin],
    data () {
      return {
        currentIndex:0, // switch的当前active的下标
        switches:[
          {name:'我喜欢的'},
          {name:'最近听的'}
        ]
      }
    },
    created () {

    },
    methods:{
      switchItem (index) { // 切换switches的方法
        this.currentIndex = index
      },
      selectSong (song, index) {
        if (index != 0) {
          const newsong = new Song(song)
          this.InsertSong(newsong)
         // this.showTip()
        }
      },
      random () { // 点击随机播放
        let list = this.currentIndex == 0 ? this.favoriteList : this.playHistory
        if (!list.length) { // 如果没有内容的话
          return false
        }
            list = list.map((song) => {
                return new Song(song)
              })
              this.randomPlay({list})
      },
      back () {
        this.$router.back()
      },
      handlePlaylist (playList) { // 在mixin中要调用的处理底部小播放器高度的函数
        const bottom = playList.length > 0 ? '60px' : ''
        this.$refs.listWrapper.style.bottom = bottom
        this.$refs.favoriteList && this.$refs.favoriteList.refresh()
        this.$refs.playHistory && this.$refs.playHistory.refresh()
      },
       ...mapActions(['InsertSong', 'randomPlay'])
    },
    components:{
      Switches,
      Scroll,
      SongList,
      NoResult
    },
    computed:{
      noreaultDesc () {
        return this.currentIndex === 0 ? '没有喜欢的歌曲' : '没有播放记录'
      },
      noresult () { // 是否展示没有结果的组件
        if (this.currentIndex === 0) {
          return !this.favoriteList.length
        } else {
          return !this.playHistory.length
        }
      },
      ...mapGetters([
        'favoriteList',
        'playHistory'
      ])
    }
  }

</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~_common/stylus/variable"

  .user-center
    position: fixed
    top: 0
    bottom: 0
    z-index: 100
    width: 100%
    background: $color-background
    &.slide-enter-active, &.slide-leave-active
      transition: all 0.3s
    &.slide-enter, &.slide-leave-to
      transform: translate3d(100%, 0, 0)
    .back
      position absolute
      top: 0
      left: 6px
      z-index: 50
      .icon-back
        display: block
        padding: 10px
        font-size: $font-size-large-x
        color: $color-theme
    .switches-wrapper
      margin: 10px 0 30px 0
    .play-btn
      box-sizing: border-box
      width: 135px
      padding: 7px 0
      margin: 0 auto
      text-align: center
      border: 1px solid  $color-text-l
      color: $color-text-l
      border-radius: 100px
      font-size: 0
      .icon-play
        display: inline-block
        vertical-align: middle
        margin-right: 6px
        font-size: $font-size-medium-x
      .text
        display: inline-block
        vertical-align: middle
        font-size: $font-size-small
    .list-wrapper
      position: absolute
      top: 110px
      bottom: 0
      width: 100%
      .list-scroll
        height: 100%
        overflow: hidden
        .list-inner
          padding: 20px 30px
    .no-result-wrapper
      position: absolute
      width: 100%
      top: 50%
      transform: translateY(-50%)
</style>



